#search_page {
  height: 100%;

  display: flex;
  flex-direction: column-reverse;
}

form {
  height: 30px;
  width: 100%;

  display: flex;
  flex-direction: row;
}

form > input, form > button {
  min-width: 0;
}

form > input[type="text"] {
  flex-shrink: 1;
}

form > input[type="submit"], form > button {
  flex-shrink: 0;
}

section {
  flex: 1;

  overflow-y: scroll;

  margin: 5px;
}

section > ol {
  margin: 0;
  padding: 0;

  list-style: none;
}

section > ol > li > img {
  max-width: 100%;
}

section > div {
  height: 1px;
  width: 1px;
}
